<template>
    <div id="http" v-loading.lock="Loading">
    <el-row class="menu" type="flex" :gutter="20" align="middle">
      <el-col :span="2"><el-button type="primary" class="add" @click="addDialogVisible = true">添加监控</el-button></el-col>
      <el-col :span="2"><el-button type="success" @click="loadMonitor(false)">刷新</el-button></el-col>
      <el-col :span="20" class="auto-ref"><el-switch
        v-model="autoRef"
        active-text="自动刷新"
        active-color="#13ce66"
        inactive-color="#ff4949">
      </el-switch></el-col>
    </el-row>

    <el-dialog
      title="添加监控"
      :visible.sync="addDialogVisible"
      width="60%">
      <el-form ref="addData" :rules="rules" :model="addData" label-width="80px">
        <el-form-item label="监控名称" prop="name">
          <el-input v-model="addData.name" placeholder="请输入内容"></el-input>
        </el-form-item>
        <el-form-item label="监控url" prop="url">
          <el-input v-model="addData.url" placeholder="https://www.baidu.com"></el-input>
        </el-form-item>
        <el-form-item label="监控频率" prop="rate">
          <el-input-number v-model="addData.rate" :min="1" :max="60" label="单位为分钟"></el-input-number>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="addMonitor">立即创建</el-button>
          <el-button @click="addDialogVisible = false">取消</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    
    <el-row class="header">
      <el-col :span="3"><div>监控名称</div></el-col>
      <el-col :span="6"><div>地址</div></el-col>
      <el-col :span="2" class="status"><div>状态</div></el-col>
      <el-col :span="8"><div>24小时可用性</div></el-col>
      <el-col :span="2"><div>频率</div></el-col>
      <el-col :span="3"><div>管理</div></el-col>
    </el-row>
    <httpstatus 
    v-for="data in list"
    v-bind:key="data.status24"
    :name="data.name"
    :id="data._id"
    :url="data.url"
    :enable="data.enable"
    :status24="data.status24"
    :rate="data.rate"
    ></httpstatus>
    </div>
</template>

<script>
import Qs from 'qs'
import axios from 'axios'
import httpstatus from './http_status.vue'
export default {
  name: 'http',
  props: {
    msg: String
  },
  data: function(){
    return { 
      autoRef:true,
      Loading: true,
        addData: {
          name: '',
          url: '',
          rate: 1,
        },
        rules: {
          name: [
            { required: true, message: '请输入监控名称', trigger: 'blur' },
          ],
          url: [
            { required: true, message: '请输入监控url', trigger: 'blur' },
            {
              pattern: /^((https|http|ftp|rtsp|mms)?:\/\/)[^\s]+/,
              message: 'url格式不对',
              trigger: 'blur'
            }
          ],
          rate: [
            { required: true, message: '间隔时间不能为空', trigger: 'blur' },
            { pattern: /^\d+$/, message: '间隔必须为数字'}
          ],
        },
        addDialogVisible: false,
        list: [],
     }
  },
  components: {
    httpstatus
  },
  created: function(){
    this.loadMonitor(false);
    let self = this;
    setInterval(function(){
      if(self.autoRef){
        self.loadMonitor(true);
        console.log('auto ref');
      }
    }, 10000);
},
methods: {
  loadMonitor: function(quiet){
    if(!quiet)this.Loading = true;
    const self = this;
    axios({ 
        method:'get',
        url:window.host + '/http_monitor/list'
    }).then(function(res){
        self.list = res.data.res.list;
        self.Loading = false;
    });
  },
  addMonitor: function(){
    let self = this;
    this.$refs['addData'].validate((valid) => {
      if (!valid) {
        return false;
      }else{
        axios({ 
          method:'post',
          headers:{'Content-Type':'application/x-www-form-urlencoded'},
          url:window.host + '/http_monitor/add',
          data: Qs.stringify(this.addData)
        }).then(function(res){
          if(res.data.code != 0){
            self.$notify.error({
            title: '错误',
            message: res.data.msg,
          });
          }else{
              self.$notify.success({
              title: '成功',
              message: '添加成功',
            });
            self.addDialogVisible = false;
            self.loadMonitor();
          }
        });
      }
    });
  }
}
}
</script>

<style>
#http{
  margin: 0 20px;
}

#http .menu{
  margin-top: 20px;
  text-align: left;
}

#http .header{
  height: 40px !important;
  font-weight: 700;
  color: #909399;
  padding-bottom: 5px;
  margin-top: 20px;
  margin-bottom: 5px;
  border-bottom: 1px solid rgb(216, 216, 216);
}

#http .auto-ref{
  text-align: right;
}
</style>