#http .el-row{
  height: 50px;
}

#http .id{
  font-size: 12px;
}

#http .name{
  text-align: left;
}

#http .url{
  color: rgb(83, 186, 255);
  cursor: pointer;
  padding: 0 5% 0 15%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  text-align: left;
}

#http .el-progress-bar__outer{
  height: 20px !important;
  margin-top: 3px;
}

#http .name-id{
  padding-left: 10px;
}

#http .http_status {
    border-bottom: 1px solid rgb(247 247 247);
}

#http .el-row{
  text-align: center;
}
</style>